@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300&display=swap);
:root {
  --width: device-width;
  --height: auto;
  --header-background-color: linear-gradient(
    90.62deg,
    #5c0088 0%,
    #8600c5 100%
  );
  --content-color: rgb(239, 221, 255);
  --hover-tint-color: rgba(255, 255, 255, 0.4);
  --header-color: #ebb4ff;
  --font: "Noto Sans KR", sans-serif;
  --background-color: #17002a;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

:root {
  --header-height: 6rem;
  --button-size: calc(var(--header-height) * 0.65);
  --icon-size: calc(var(--button-size) * 0.75);
  --margin: 1rem;
  --margin-secondary: calc(var(--margin) * 0.5);
  --name-height: calc(var(--header-height) / 2);
}

.header {
  /* Header Background */
  z-index: 1;
  position: fixed;
  width: 100%;
  height: 6rem;
  height: var(--header-height);
  left: 0px;
  top: 0px;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;

  /* Header Background */

  background: var(--header-background-color);
}

.logo-container {
  margin: 1rem;
  margin: var(--margin);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.spacer {
  -webkit-flex-grow: 10;
          flex-grow: 10;
}
.socials {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 1rem;
  margin: var(--margin);
}

.button {
  /* Button */
  width: calc(6rem * 0.65);
  width: var(--button-size);
  height: calc(6rem * 0.65);
  height: var(--button-size);
  border-radius: 25%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: calc(1rem * 0.5);
  margin: var(--margin-secondary);
  transition: 0.5s;
}

.button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background: var(--hover-tint-color);
}

.icon {
  width: calc(calc(6rem * 0.65) * 0.75);
  width: var(--icon-size);
  height: calc(calc(6rem * 0.65) * 0.75);
  height: var(--icon-size);
}

.logo {
  width: calc(6rem * 0.65);
  width: var(--button-size);
  height: calc(6rem * 0.65);
  height: var(--button-size);
  margin: calc(1rem * 0.5);
  margin: var(--margin-secondary);
}

.name {
  font-family: var(--font);
  font-size: calc(6rem / 2);
  font-size: var(--name-height);
  font-weight: bold;
  color: var(--header-color);
  margin: calc(1rem * 0.5);
  margin: var(--margin-secondary);
}

:root {
  --content-size: 2rem;
  --img-size: calc(5 * var(--content-size));
  --img-margin: calc(2 * var(--content-size));
  --resource-size: calc(1.5 * var(--img-size));
}

.content {
  /* Background */
  position: absolute;
  width: var(--width);
  height: var(--height);
  left: 0px;
  top: var(--header-height);
  z-index: 0;
  background: var(--background-color);

  font-size: 2rem;

  font-size: var(--content-size);
  font-family: var(--font);
  color: var(--content-color);
}

.text {
  margin: 25%;
  margin-top: 2rem;
  margin-top: var(--content-size);
  margin-bottom: calc(2 * 2rem);
  margin-bottom: var(--img-margin);
}

.resources {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.resource {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  margin: calc(2 * 2rem);

  margin: var(--img-margin);
  width: calc(1.5 * calc(5 * 2rem));
  width: var(--resource-size);
  height: calc(1.5 * calc(5 * 2rem));
  height: var(--resource-size);

  transition: 0.5s;
  border-radius: 1.5rem;
  color: var(--content-color);
}

.resource:hover {
  background: var(--hover-tint-color);
}

.footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.footer span {
  margin: 2rem;
  margin: var(--content-size);
}

.trans {
  background: linear-gradient(
    90deg,
    #55cdfc 0%,
    #55cdfc 12%,
    #f7a8b8 22%,
    #f7a8b8 35%,
    #ffffff 45%,
    #ffffff 55%,
    #f7a8b8 65%,
    #f7a8b8 77%,
    #55cdfc 87%,
    #55cdfc 100%
  );
  font-weight: 600;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.image {
  width: calc(5 * 2rem);
  width: var(--img-size);
  height: calc(5 * 2rem);
  height: var(--img-size);
}

